import { Component } from 'react'
import { contactService } from '../services/contact.service'
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Link } from 'react-router-dom'
import TransferFund from '../cmps/TransferFund.jsx';
import { MovesList } from '../cmps/MovesList.jsx'
import { UserService } from '../services/user.service'

export class ContactDetails extends Component {

    state = {
        contact: null,
        user: null
    }

    componentDidMount() {
        this.loadContact()
        this.loadUser()
    }

    componentDidUpdate(prevProps, prevState) {
        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.loadContact()
        }
    }

    loadContact = async () => {
        try {
            const contact = await contactService.getContactById(this.props.match.params.id)
            this.setState({ contact })

        } catch (error) {
            console.log('error:', error)
        }
    }

    loadUser = async () => {
        try {
            const user = await UserService.getUser()
            this.setState({ user })
        } catch (error) {
            console.log('error:', error)
        }
    }

    onBack = () => {
        this.props.history.push('/contact')
    }

    onTransferCoins = (coins) => {
        UserService.addMove(this.state.contact, coins)
        this.props.history.push('/')


    }
    test = () => {
        console.log(this.state.user.moves.filter(move => move.toId === this.state.contact._Id));
    }

    render() {
        const { contact, user } = this.state
        if (!contact) return <div>Loading...</div>
        return (
            <section className='contact-details'>
                <section>
                    <h3>Name: {contact.name}</h3>
                </section>
                <section>
                    <h3>email: {contact.email}</h3>
                </section>
                <section>
                    <h3>phone: {contact.phone}</h3>
                </section>
                <img src={`https://robohash.org/set_set5/bgset_bg1/${contact._id}?size=300x300`} />
                <div><Button variant="outline-secondary" onClick={this.onBack}>Back</Button >
                    <Link to={`/contact/edit/${contact._id}`}><Button className="edit-btn" variant="outline-secondary" >Edit</Button></Link>
                </div>
                <TransferFund contact={contact} maxCoins={user.coins} onTransferCoins={this.onTransferCoins} />
                <MovesList moves={user.moves.filter(move => move.to === contact.name)} />
            </section>
        )
    }
}
