import React from 'react'
import { Link, NavLink, withRouter } from "react-router-dom";

export function Header() {
  return (
    <>
      <div className=" header flex space-between justify-content">
        <NavLink exact to="/" >
          <div className='logo-container flex'>
            <img className="logo-img" src={require('../img/logo.png')} />
            <div>Mister Bitcoin</div>
          </div>
        </NavLink>
        <nav className="flex " >
          <NavLink exact to="/contact" ><img src={require('../img/phone-book.png')} /></NavLink>
          <NavLink exact to="/chart" ><img src={require('../img/static.png')} /></NavLink>
        </nav>
      </div>
    </>
  )
}
