import React, { useState } from 'react'
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css'
import { InputGroup, FormControl } from 'react-bootstrap';
import Form from 'react-bootstrap/Form'

export default TransferFund

function TransferFund({ contact, maxCoins, onTransferCoins }) {

  const [coins, setCoins] = useState(0)

  const handleTransfer = () => {
    onTransferCoins(coins)
    setCoins(0)
  }

  return (
    <div>
      <h2 className='transfer-title'>Transfer funds to: {contact.name}</h2>
      <Form onSubmit={handleTransfer} >
        <InputGroup className="mb-3">
          <InputGroup.Text htmlFor="phone" id="inputGroup-sizing-default">
            Amount
          </InputGroup.Text>
          <Form.Control
            type="number"
            value={coins}
            onChange={(e) => setCoins(parseInt(e.target.value))}
            max={maxCoins}
            min={0}
            id="inputGroup-sizing-default"
          />
        </InputGroup>
        <Button variant="outline-secondary" type="submit">Transfer</Button>
      </Form>
    </div>
  )
}