import React from 'react';
export function MovesList({ moves }) {
  if (moves.length === 0) return <h2>No moves</h2>
  return (
    <div className='moves-list'>
      {moves.length === 3 ? <h2>Your last 3 moves</h2> : <h2>Your moves</h2>}
      {moves.map((move) => (
        <div className='move' key={move.toId}>
          <p>Amount: {move.amount}</p>
          <p>To: {move.to}</p>
          <p>Date: {move.at}</p>
        </div>
      ))}
    </div>
  )
}


