import React, { Component } from 'react'
import { contactService } from '../services/contact.service'
import Form from 'react-bootstrap/Form'
import { InputGroup, FormControl } from 'react-bootstrap';
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css'

export class ContactEdit extends Component {

    state = { contact: contactService.getEmptyContact(),}

    async componentDidMount() {
        const contactId = this.props.match.params.id
        if (contactId) {
            try {
                const contact = await contactService.getContactById(contactId)
                this.setState({ contact })

            } catch (error) {
                console.log('error:', error)
            }
        }
    }

    onSaveContact = async (ev) => {
        console.log('save');
        ev.preventDefault()
        try {
            await contactService.saveContact({ ...this.state.contact })
            this.props.history.push('/contact')
        } catch (error) {
            console.log('error:', error)
        }
    }

    onRemoveContact = async (contactId) => {
        console.log('onRemoveContact');
        try {
            await contactService.removeContact(contactId)
            this.props.history.push(`/contact`)

        } catch (error) {
            console.log('error:', error)
        }
    }

    handleChange = ({ target }) => {
        const field = target.name
        let value = target.value

        switch (target.type) {
            case 'phone':
                value = +value
                break;
        }
        this.setState(({ contact }) => ({ contact: { ...contact, [field]: value } }))
        console.log(this.setState(({ contact }) => ({ contact: { ...contact, [field]: value } })));
    }

    render() {
        const { contact } = this.state
        const { name, email, phone } = contact
        return (
            <section className='contact-edit'>
                <h1>{contact._id ? 'Edit' : 'Add'} Contact</h1>
                <div className='btns'>
                    {contact._id && <Button onClick={() => this.props.history.push(`/contact/${contact._id}`)} variant="outline-secondary">Back</Button>}
                    {!contact._id && <Button onClick={() => this.props.history.push(`/contact`)} variant="outline-secondary">Back</Button>}
                    {contact._id && <Button variant="outline-secondary" onClick={() => this.onRemoveContact(contact._id)}>Delete</Button>}
                </div>


                <img src={`https://robohash.org/set_set5/bgset_bg1/${contact._id}?size=300x300`} />

                <Form onSubmit={this.onSaveContact} >
                    <InputGroup className="mb-3">
                        <InputGroup.Text htmlFor="phone" id="inputGroup-sizing-default">
                            Phone:
                        </InputGroup.Text>
                        <Form.Control
                            onChange={this.handleChange} value={phone} type="text" name="phone" id="phone"
                            aria-label="Default"
                            aria-describedby="inputGroup-sizing-default"
                            autoComplete="off"
                        />
                    </InputGroup>

                    <InputGroup className="mb-3">
                        <InputGroup.Text htmlFor="name" id="inputGroup-sizing-default">
                            Name:
                        </InputGroup.Text>
                        <Form.Control
                            onChange={this.handleChange} value={name} type="name" name="name" id="name"
                            aria-label="Default"
                            aria-describedby="inputGroup-sizing-default"
                            autoComplete="off"
                        />
                    </InputGroup>

                    <InputGroup className="mb-3">
                        <InputGroup.Text htmlFor="email" id="inputGroup-sizing-default">
                            Email:
                        </InputGroup.Text>
                        <Form.Control
                            onChange={this.handleChange} value={email} type="email" name="email" id="email"
                            aria-label="Default"
                            aria-describedby="inputGroup-sizing-default"
                            autoComplete="off"
                        />
                    </InputGroup>
                    <Button variant="outline-secondary" type="submit">Save</Button>
                </Form>
            </section>)
    }
}
