function getUser() {
    const loggedUser = getLoggedinUser()
    if (!loggedUser) return null
    return gUser

}


function login(name) {

    let userNames = JSON.parse(localStorage.getItem(USER_NAMES_KEY)) || [];
    if (!userNames.includes(name)) {
        return null
    } else {
        localStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(name))
        return gUser
    }
}


function signup(name) {
    let userNames = JSON.parse(localStorage.getItem(USER_NAMES_KEY)) || [];
    userNames.push(name);
    localStorage.setItem(USER_NAMES_KEY, JSON.stringify(userNames));
    localStorage.setItem(STORAGE_KEY_LOGGEDIN_USER, JSON.stringify(name));
    gUser.name = name
}

function addMove(contact, amount) {
    gUser.coins = gUser.coins - amount
    const currentDate = new Date()
    const day = currentDate.getDate()
    const month = currentDate.getMonth() + 1
    const year = currentDate.getFullYear()
    const hours = currentDate.getHours()
    const minutes = currentDate.getMinutes().toString().padStart(2, '0')
    const seconds = currentDate.getSeconds()

    const move = {
        toId: `${contact._id}`,
        to: `${contact.name}`,
        at: (`${day}/${month}/${year} ${hours}:${minutes}:${seconds}`),
        amount,
    }
    gUser.moves.push(move)
}

function getLoggedinUser() {
    return JSON.parse(localStorage.getItem(STORAGE_KEY_LOGGEDIN_USER))
}
function logout() {
    localStorage.removeItem(STORAGE_KEY_LOGGEDIN_USER)
}