import React, { Component } from 'react'
import { UserService } from '../services/user.service'
import { BitcoinService } from '../services/bitcoin.service'
import { ColorRing } from 'react-loader-spinner'
import Form from 'react-bootstrap/Form'
import { InputGroup, FormControl } from 'react-bootstrap';
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css'
import { MovesList } from '../cmps/MovesList'

export class HomePage extends Component {
    state = {
        user: null,
        bitcoinRate: null,
        name: ''
    }

    componentDidMount() {
        this.loadUser()
        this.loadbitcoinRate()
    }

    loadUser = async () => {
        try {
            const user = await UserService.getUser()
            this.setState({ user })
        } catch (error) {
            console.log('error:', error)
        }
    }

    loadbitcoinRate = async () => {
        try {
            const bitcoinRate = await BitcoinService.getRate()
            this.setState({ bitcoinRate })
        } catch (error) {
            console.log('error:', error)
        }
    }


    handleSubmit = (event) => {
        event.preventDefault();
        const name = this.state.name;
        const user = UserService.login(name)
        if (!user) {
            this.setState({ user })
            this.props.history.push('/signup')
        }
        else this.setState({ user })
    }

    handleChange = (event) => {
        this.setState({
            name: event.target.value
        })
    }

    logut = () => {
        UserService.logout()
        this.setState({ user: null })
    }

    render() {
        const { user, bitcoinRate } = this.state
        if (!bitcoinRate) return <div className='loader'><ColorRing
            visible={true}
            height="380"
            width="380"
            ariaLabel="blocks-loading"
            wrapperStyle={{}}
            wrapperClass="blocks-wrapper"
            colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
        /></div>

        if (!user) return <section className="log-in">
            <div className='welcome'>Welcome to Mister Bitcoin!</div>
            <div className="logo-img"><img src={require('../img/logo.png')} /></div>


            <h2>Please log in</h2>
            <form onSubmit={this.handleSubmit} >
                <InputGroup className="mb-3">
                    <InputGroup.Text
                        id="inputGroup-sizing-default">
                        Name:
                    </InputGroup.Text >
                    <Form.Control

                        aria-label="Default"
                        aria-describedby="inputGroup-sizing-default"
                        autoComplete="off"
                        value={this.state.name}
                        onChange={this.handleChange}
                    />
                </InputGroup>
                <Button variant="outline-secondary" type="submit">Log in</Button>
            </form>
        </section>

        return (
            <>
                <section className="home-page">

                    <h1> Hello {this.state.user.name} !</h1>
                    <div className="coins-info flex ">
                        <img src={require('../img/coins.png')} />
                        <div>Coins: {this.state.user.coins}</div>
                    </div>
                    <div className="bitcoin-info flex ">
                        <img src={require('../img/bitcoin.png')} />
                        <div>BTC:{this.state.bitcoinRate} </div>

                    </div>
                    <div className='logout'><Button className='btn-logout' onClick={this.logut} variant="outline-secondary" type="submit">Log out</Button></div>
                    <MovesList moves={this.state.user.moves.reverse().slice(0, 3)} />
                </section>
            </>
        )
    }
}
