import React, { Component } from 'react'
import { contactService } from '../services/contact.service.js'
import { ContactList } from '../cmps/ContactList.jsx';
import { ContactDetails } from './ContactDetails.jsx'
import { ContactFilter } from '../cmps/ContactFilter.jsx'
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css'
import { Link } from 'react-router-dom'
import { ColorRing } from 'react-loader-spinner'

export class ContactPage extends Component {
    state = {
        contacts: null,
        selectedContactId: null,
        filterBy: {
            name: '',
        }
    }

    componentDidMount() {
        this.loadContacts()
    }

    loadContacts = async () => {
        try {
            const contacts = await contactService.getContacts(this.state.filterBy)
            this.setState({ contacts });
        } catch (error) {
            console.log('error:', error)
        }
    }

    onRemoveContact = async (contactId) => {
        try {
            await contactService.removeContact(contactId)
            this.setState(({ contacts }) => ({
                contacts: contacts.filter(contact => contact._id !== contactId)
            }))

        } catch (error) {
            console.log('error:', error)
        }
    }

    onChangeFilter = (filterBy) => {
        this.setState({ filterBy: { ...filterBy } }, this.loadContacts)
    }

    render() {
        const { contacts, selectedContactId, filterBy } = this.state
        if (!contacts) return <ColorRing
            visible={true}
            height="80"
            width="80"
            ariaLabel="blocks-loading"
            wrapperStyle={{}}
            wrapperClass="blocks-wrapper"
            colors={['#e15b64', '#f47e60', '#f8b26a', '#abbd81', '#849b87']}
        />
        
        return (
            <section className='contacts'>
                <>
                    <ContactFilter filterBy={filterBy} onChangeFilter={this.onChangeFilter} />
                    <div className="add-contact"><Link to="/contact/edit"><Button variant="outline-secondary">Add contact</Button></Link></div>
                    <ContactList contacts={contacts} onRemoveContact={this.onRemoveContact} />
                </>
            </section>

        )
    }
}

